@import url(https://fonts.googleapis.com/css2?family=Maven+Pro&family=Squada+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  padding: 20px;
  /* background-color: #451804; */
  background-image: url('https://res.cloudinary.com/hannahbannan/image/upload/v1593207017/Mars%20Project/193855_planet-mars-wallpaper_qhuqhi.jpg');
  /* background-size:cover; */
  background-position: center;
  background-size: cover;
  background-color: black;
  height: 80vh;
  color: white;
}

header {
  position: left;
  text-align: left;
  position: fixed;
  padding-left: 20px;
  top: 0;
  left: 0;
  z-index: 1000000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

main {
  position: center;
  text-align: center;
  padding-top: 120px;
  padding-bottom: 80px;
  color: white;
  /* background-color: rgba(0,0,0,0.2) */
}

.About {
  padding: 0px 20px 20px 20px;
}

footer {
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100vw;
  height: 20px;
  background-color: rgb(0,0,0);
  display: flex;
  align-items: center;
}

footer p {
  color: darkred;
  text-align: left;
  padding-left: 10px;
  font-size: 12px;
}

/* Weather Report Page */


.weather-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, auto);
  grid-gap: 5px;
  position: center;
  padding: 10px;
}

@media screen and (min-width:768px) {

  .App {
    height: 100vh;
  }

  .weather-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, auto);
    grid-gap: 10px;
  }
}

h4 {
    line-height: 12px;
}

[class="0"] {
    grid-area: 1/1/2/4;
    margin-bottom: 80px;
    line-height: 0.8;
}

[class='0'] h4{
    font-size: 1.5rem;
    line-height: 0;
} 
[class='0'] h2{
    font-size: 3rem;;
} 
[class='0'] p{
    color: #ECECEC;
}


[class='1'], [class='2'], [class='3'], [class='4'], [class='5'], [class='6'], h4 {
    font-size: 0.8rem;
    line-height: 0.5;
} 
  
[class="1"] {
    grid-area: 2/1/3/2;
    background-color: rgba(116,0,0,0.6);
}
  
[class="2"] {
    grid-area: 2/2/3/3;
    background-color: rgba(116,0,0,0.6);
}
  
[class="3"] {
    grid-area: 2/3/3/4;
    background-color: rgba(116,0,0,0.6);
}
  
[class="4"] {
    grid-area: 3/1/4/2;
    background-color: rgba(116,0,0,0.6);
}
  
[class="5"] {
    grid-area: 3/2/4/3;
    background-color: rgba(116,0,0,0.6);
}
  
[class="6"] {
    grid-area: 3/3/4/4;
    background-color: rgba(116,0,0,0.6);
}

@media only screen and (min-width: 768px) {


    [class="0"] {
        grid-area: 1/1/2/7;
        margin-bottom: 40px;
      }
      
    [class="1"] {
        grid-area: 2/1/3/2;
        background-color: rgba(0,0,0,0.3);
    }
      
    [class="2"] {
        grid-area: 2/2/3/3;
        background-color: rgba(0,0,0,0.3)
    }
      
    [class="3"] {
        grid-area: 2/3/3/4;
        background-color: rgba(0,0,0,0.3)
    }
      
    [class="4"] {
        grid-area: 2/4/3/5;
        background-color: rgba(0,0,0,0.3)
    }
      
    [class="5"] {
        grid-area: 2/5/3/6;
        background-color: rgba(0,0,0,0.3)
    }
      
    [class="6"] {
        grid-area: 2/6/3/7;
        background-color: rgba(0,0,0,0.3)
    }
}
.hamburger {
    color: #e0e2e1;
    position: fixed;
    right: 0px;
    top: 20px;
}
h2 {
    font-family: 'Squada One', serif;
}
  
.hamburger div {
    width: 30px;
    height: 5px;
    background-color: white;
    margin: 6px 12px;
    cursor: pointer;
    margin-right: 20px;;
}
  
a {
    color: white;
    font-family: "Maven Pro", sans-serif;
    text-decoration: none;
}
  
a:hover {
    color: #F9AD57;
}
